body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b9deec;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  margin-bottom: 0;
  
  border-bottom: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 62px;
  background-color: #4682b4;
  font-size:large;

}
.header a {
  margin: 0 1em 0 1em;
  font-size: 1em;
  font-weight: 600;
  color:rgb(249, 249, 239);
  text-decoration: none;
  padding: 7% 1% 8% 2%;
}

.content {
  margin-left: 2em;
  font-size: 1.3em;
}

.message {
  color: red;
  font-size: 0.7em;
}
input[type="text"],
input[type="password"]{
  width: 85%;
  padding: 6px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 20px;
  background-color: rgb(252, 237, 248);
}
input[type="submit"] {
  padding: 10px 20px;
  color:rgb(249, 249, 239);
  font-size: large;
  font-weight: 700;
  position: absolute;
  right: 10%;
  border-radius: 12px;
  border: 1px solid;
  background-color: #4682b4;
  text-align: center;
}


.login-layout, .register-layout{
  padding: 3% 0 25% 10%;
  margin: 5% 2% 0 2%;
  background-color: white;
  border-radius: 7%;
}
h5 {
  font-size: 17px;
}
.account-layout {
  font-weight: 500;
  color: aliceblue;
  padding: 10px 20px 17px 10px;
  margin: 0 0 0 0;
  background-color: #165789;
}

.dropdown-input {
  display: block;
}

.dropdown-button, #menu-toggle{
  color: white;
  display: inline-block;
  position: absolute;
  top: 5px;
  right: -1.8em;
  width: 80px;
  height: 30px;
  font-size: 25px;
}
#menu-toggle {
  z-index: 1;
  display: none;
  
}
#dropdown-content {
  display: none;
  position: absolute;
  right: -2em;
  left: auto;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  align-items: right;
  transition: left 2s ease;
}
#menu-toggle:checked + .dropdown-button + #dropdown-content {
  display: block;
}
